import React, { useState, useEffect } from 'react';
import tailwind from "@theme-ui/preset-tailwind"

const LinkText = ({ text, url }) => {

  const [isHovered, setIsHovered] = useState(false);

  const handleMouseEnter = () => {
    setIsHovered(true);
  };

  const handleMouseLeave = () => {
    setIsHovered(false);
  };

  const textDecoration = isHovered ? 'underline' : 'none';

  const [itemValue, setItemValue] = useState('');

  useEffect(() => {
    const handleStorageChange = (event) => {
      const initialValue = localStorage.getItem('theme-ui-color-mode');
      if (initialValue) {
        console.log(initialValue)
        setItemValue(initialValue);
      }
    };

    // Add event listener to observe storage changes
    window.addEventListener('click', handleStorageChange);

    // Retrieve initial value from localStorage
    const initialValue = localStorage.getItem('theme-ui-color-mode');
    if (initialValue) {
      setItemValue(initialValue);
    }

    return () => {
      // Clean up the event listener on component unmount
      window.removeEventListener('click', handleStorageChange);
    };
  }, []);

  const style = {
    color: itemValue === 'dark' ? tailwind.colors.purple[5] : tailwind.colors.purple[7],
    textDecoration: textDecoration
  };

  return (
    <a style={style} href={url} target="_blank" rel="nofollow noopener noreferrer" onMouseEnter={handleMouseEnter}
      onMouseLeave={handleMouseLeave}>
      {text}
    </a>
  );
};

export default LinkText;
